import { useState } from "react";
import styled from "styled-components";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { CopyOutlined } from "@ant-design/icons";

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  perspective: 1000px;
  font-family: "VT323", sans-serif;
  background-color: #1c1c1c;

  div {
    line-height: 1.2;
  }
`;

const VisitingCard = styled.div`
  height: 450px;
  width: 320px;
  position: relative;
  transform-style: preserve-3d;
  box-sizing: border-box;

  .back,
  .front {
    z-index: 2;
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    display: block;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
    padding: 32px;
    position: absolute;
    border-radius: 4px;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    transition: -webkit-transform ease 1000ms;
    transition: transform ease 1000ms;
    color: #ebebeb;
    background: #2d2d2e;
  }

  .front {
    transform: ${(props) => props.flip && `rotateY(180deg)`};
  }

  .back {
    transform: ${(props) => props.flip && `rotateY(0deg)`};
  }

  a {
    padding-left: 8px;
    color: #66adff;
    position: relative;
    font-size: 18px;
    img {
      position: relative;
      top: 2.5px;
      margin-left: 8px;
    }
  }

  a:first-child {
    padding-left: 0;
  }
`;

const VisitingCardFront = styled.div`
  transform: rotateY(0deg);
  display: flex;
  align-items: center;
  justify-content: center;
`;

const VisitingCardBack = styled.div`
  transform: rotateY(-180deg);
`;

const Initials = styled.div`
  padding: 0;
  margin: 0;
  text-align: center;
  font-size: 108px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Name = styled.div`
  margin-top: 32px;
  font-size: 48px;
`;

const Role = styled.div`
  font-size: 20px;
  margin-bottom: 64px;
`;

const Flip = styled.div`
  text-align: right;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  flex: 1;
  justify-content: flex-end;
`;

const Email = styled.div`
  margin-top: 8px;
  font-size: 20px;
`;

const Phone = styled.div`
  font-size: 20px;
`;

const Location = styled.div`
  font-size: 20px;
`;

const Footer = styled.div`
  display: flex;

  span {
    float: right;
    display: flex;
    flex: 1;
    justify-content: flex-end;
  }
`;

const Flex = styled.div`
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: calc(100% - 20px);
`;

const FlexWrap = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Copied = styled.span`
  color: #66adff;
  font-size: 18px;
  padding: 0 8px;
  border: 1px solid #66adff;
`;

function App() {
  const [isFlipped, setIsFlipped] = useState(false);
  const [copied, setCopied] = useState(false);

  const handleFlip = () => {
    setIsFlipped(!isFlipped);
  };

  const handleCopy = () => {
    setCopied(true);
    setTimeout(() => {
      setCopied(false);
    }, 2000);
  };

  const FlipComponent = () => <Flip onClick={handleFlip}>Flip</Flip>;

  return (
    <Container>
      <VisitingCard flip={isFlipped}>
        <VisitingCardFront className="front">
          <FlipComponent />
          <Initials className="initials">KS</Initials>
        </VisitingCardFront>
        <VisitingCardBack className="back">
          <FlexWrap>
            {copied ? <Copied>Copied!</Copied> : ""}
            <FlipComponent />
          </FlexWrap>
          <Flex>
            <div>
              <Name>Kartik Sharma</Name>
              <Role>Software Engineer</Role>
              <Email>
                Email:- ks.jc1995@gmail.com
                <span title={"Copy"}>
                  <CopyToClipboard
                    text="ks.jc1995@gmail.com"
                    onCopy={handleCopy}
                  >
                    <CopyOutlined style={{ fontSize: 12, paddingLeft: 8 }} />
                  </CopyToClipboard>
                </span>
              </Email>
              <Phone>
                Phone:- +917018938106
                <span title={"Copy"}>
                  <CopyToClipboard onCopy={handleCopy} text="+917018938106">
                    <CopyOutlined style={{ fontSize: 12, paddingLeft: 8 }} />
                  </CopyToClipboard>
                </span>
              </Phone>
              <Location>Location:- India</Location>
            </div>
            <Footer>
              <a
                href="https://github.com/ksjc1995/ksjc1995.github.io/raw/master/kartik_sharma.pdf"
                download
              >
                Resume
              </a>
              <span>
                <a
                  href="https://www.linkedin.com/in/kartik-sharma-742b4867/"
                  target="_blank"
                  rel="noreferrer"
                >
                  LinkedIn
                </a>
                <a
                  href="https://github.com/ksjc1995"
                  target="_blank"
                  rel="noreferrer"
                >
                  Github
                </a>
              </span>
            </Footer>
          </Flex>
        </VisitingCardBack>
      </VisitingCard>
    </Container>
  );
}

export default App;
